

/*------------------------@use------------------------------------------*/
@use "nav";
@use "animation";
@use "variabels";
@use "mixin";


/*------------------------Dark------------------------------------------*/

@media (prefers-color-scheme:dark) {

  body {
    background-color: variabels.$darktheme-background !important;
    font-family: variabels.$font-style;
    width: 100vw;
    overflow-x: hidden;
  }
  header {
    background-color: variabels.$headerbgdark-color !important;
    width: 100vw;
    margin-left: -2vw;
    margin-top: -3vh;
  }

/*----------------------Bilder grids--------------------------------------------*/
.picGrid {
  @include mixin.grid1x2;
  

  img {
    @include mixin.imageDark;
}
}

.picGrid2 {
  @include mixin.grid1x2;
  

  img {
    @include mixin.imageDark (50vw);
    
}
}

/*------------------------h1------------------------------------------*/
.h1Change{
  @include mixin.h1Change;
}

/*------------------------SASS------------------------------------------*/
.SASS {
  width: 50vw;
  margin: auto;

  h1{
    @include mixin.h1Change;
  }

  p{
    color: variabels.$text-color !important;
    text-align: left;
  }
}

/*------------------------Krav------------------------------------------*/
.hemsidaKrav {
  @include mixin.hemsidaText;

  h2{
    color: variabels.$text-color !important;
    text-decoration: underline;
  }

  p{
    color: variabels.$text-color !important;
  }
}


.sassKrav {
  @include mixin.hemsidaText;

}

/*------------------------Animationer Text------------------------------------------*/
  
#animationText {
  text-align: center;
  width: fit-content;
  font-size: 200%;
  animation-name: changeTextColor;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  border-radius: 50%;
  padding: 2%;
  position: relative;

}


/*------------------------Animationer Knapp------------------------------------------*/

.buttonDiv{
  text-align: center;
  margin-top: 20vh;
  position: relative;
 
}

#buttonAnimation {
  text-align: center;
  height: 5vh;
  width: 20vw;
  font-weight: 900;
  font-size: 120%;
  position: relative;
  
}


/*------------------------Animationer race------------------------------------------*/



#bil1Animation {
    img{
      width: 7vw;
      margin-top: 3vh;
      
    }
  position: relative;
  
  
 }
 
 #bil2Animation{
  img{
    width: 7vw;
  }
   position: relative;
  
 }

 #allaBilar {
  background-image: url(/bilder/bana.jpg);
  background-repeat:no-repeat;
  background-size:contain;
  height: 25vh;
  width: 88vw;
  position: relative;
  margin-left: 5vw;
  

 }



